/* frontend/src/App.css */
.App {
  font-family: Arial, sans-serif;
}

.App-header {
  display: flex;
  align-items: center;
  padding: 10px;
  background-color: #f2f2f2;
}

.App-logo {
  height: 50px;
  margin-right: 10px;
}

.App-body {
  display: flex;
  min-height: calc(100vh - 70px);
}

.App-nav {
  width: 200px;
  background-color: #e6e6e6;
  padding: 20px;
}

.App-nav ul {
  list-style-type: none;
  padding: 0;
}

.App-nav li {
  margin: 10px 0;
}

.App-nav a {
  text-decoration: none;
  color: #333;
}

.App-main {
  flex: 1;
  padding: 20px;
}
